.about {
  padding-bottom: 30px;
  @media screen and (min-width: 768px) {
    padding-bottom: 10px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 80px;
  }
}

.about__title {
  text-align: center;
  font-size: 24px;
  line-height: 2.7;
  @media screen and (min-width: 768px) {
    text-align: start;
  }
  @media screen and (min-width: 1280px) {
    font-size: 36px;
    line-height: 1.8;
  }
}
.about__text {
  font-size: 16px;
  line-height: 1.25;
  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.2;
  }
}
.about__item:not(:last-child) {
  margin-bottom: 15px;
}

.about__content {
  @media screen and (min-width: 768px) {
    width: 50%;
    margin-left: auto;
  }
}
